.searchbar {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 15px;
    background-color: rgb(60, 69, 77);
    border-radius: 10px;
    width: 100%;
    height: 30px;
}

.searchbar-input {
    color: white;
    padding-left: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 15px;
    background-color: rgb(60, 69, 77);
    border-radius: 10px;
    border-top-right-radius: 0px;
    border-bottom-right-radius: 0px;
    width: 100%;
    height: 28px;
    border: none;
}

.searchbar-btn {
    color: white;
    background-color: rgb(72, 81, 88);
    border: none;
    height: 30px;
    border-radius: 10px;
    border-top-left-radius: 0px;
    border-bottom-left-radius: 0px;

    transition: all 0.3s;
}

.searchbar-btn:hover {
    transform: scale(1.05)
}