@import url('https://fonts.googleapis.com/css2?family=Roboto&family=Space+Mono&display=swap');

.general-data-container {
    font-family: monospace;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: flex-start;
    min-height: 25vh;
}

.general-condition {
    font-size: 50px;
}

.general-city {
    font-size: 23px;
}

.general-date {
    font-size: 15px
}

.general-time {
    font-size: 15px
}

.general-temp-c {
    font-size: 40px;
    font-weight: bold;
    margin: 10px 0px;
}

.general-searchbar {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 15px;
    background-color: rgb(60, 69, 77);
    border-radius: 10px;
    width: 100%;
    height: 30px;
}